.accordion { clear: both; margin-bottom: 4rem; border-bottom: 1px solid #dddddd; }

.accordion__item { border-top: 1px solid #dddddd; }

.accordion__item[open] .accordion__title { font-weight: 500; background: #f0f1f2; }

