.accordion__toggle { display: block; padding-left: 1.5em; }

.accordion__toggle:before { display: inline-block; float: left; content: ''; width: 1em; height: 1em; line-height: 1; padding: 0; margin-left: -1.5em; background: #41464b url("//www.staffs.ac.uk/site/images/icons/icon-plus-white.svg") no-repeat center center; background-size: 50%; text-align: center; border-radius: 50%; vertical-align: middle; box-sizing: border-box; }

